import React, { Component } from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CInput,
  CLabel,
} from "@coreui/react";

import API from "src/api";

import SunEditor from "suneditor-react";
import "suneditor/dist/css/suneditor.min.css";

import { history } from "src/history";

const editorConfig = [
  ["align", "fontColor", "fontSize"],
  ["bold", "underline", "italic", "strike", "subscript", "superscript"],
  ["undo", "redo"],
  ["outdent", "indent"],
];

class AddUnit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      content: "",
      topicId: this.props.match.params.id,
    };
    this.handleInputChange = this.handleInputChange.bind(this);
    this.submitButton = this.submitButton.bind(this);
  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;

    this.setState({
      [name]: value,
    });
  }

  handleEditorChange(content) {
    this.setState({
      content: content,
    });
  }

  async submitButton() {
    await API.post("/unit/add", this.state);
    this.resetButton();
  }

  resetButton = () => {
    this.setState((prevState) => ({
      title: "",
      content: "",
    }));
  };

  render() {
    let { title, content } = this.state;
    return (
      <>
        <CCol xs="12">
          <CCard>
            <CCardHeader>
              <span className="h4">Add Unit</span>
              <CButton
                className="btn btn-outline-info float-right mx-2"
                onClick={() => {
                  history.goBack();
                }}
              >
                Back
              </CButton>
            </CCardHeader>
            <CCardBody>
              <CForm
                action=""
                method="post"
                encType="multipart/form-data"
                className="form-horizontal"
              >
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="title">Title</CLabel>
                  </CCol>
                  <CCol xs="12" md="6">
                    <CInput
                      onChange={this.handleInputChange}
                      id="title"
                      name="title"
                      value={title}
                      placeholder="Title"
                    />
                  </CCol>
                </CFormGroup>
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="content">Content</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    {/* <CInput onChange={this.handleInputChange} type="text" id="subTitle" name="subTitle" placeholder="Enter Chapter SubTitle"/>                     */}
                    <SunEditor
                      defaultValue={content}
                      setOptions={{ buttonList: editorConfig }}
                      height="70%"
                      onChange={(content) => {
                        this.handleEditorChange(content);
                      }}
                    />
                  </CCol>
                </CFormGroup>
              </CForm>
            </CCardBody>
            <CCardFooter>
              <CButton
                type="submit"
                className="m-1"
                size="sm"
                color="primary"
                onClick={this.submitButton}
              >
                Submit
              </CButton>
              <CButton
                type="reset"
                size="sm"
                className="m-1"
                color="danger"
                onClick={this.resetButton}
              >
                Reset
              </CButton>
            </CCardFooter>
          </CCard>
        </CCol>
      </>
    );
  }
}

export default AddUnit;
